/* Container */
.create-container {
  max-width: 90%;
  width: 600px;
  margin: 2rem auto;
  padding: 2rem;
  background: #fff;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.create-container:hover {
  transform: scale(1.02);
}

/* Form Layout */
.create-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

/* Input Fields */
.input-field {
  padding: 0.75rem;
  border: 2px solid #f4c6d9;
  border-radius: 8px;
  font-size: 1rem;
  width: 100%;
  box-sizing: border-box;
  transition: all 0.3s ease;
}

.input-field:focus {
  border-color: #f8a1c2;
  outline: none;
}

/* Submit Button */
.submit-button {
  padding: 1rem;
  background: #f8a1c2;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: transform 0.2s, background 0.3s ease;
}

.submit-button:hover {
  transform: scale(1.05);
  background: #f68f9d;
}

/* File Upload */
.file-upload-container {
  margin: 1rem 0;
}

.file-upload-label {
  padding: 0.75rem 1.5rem;
  background: #f8a1c2;
  color: white;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1rem;
}

.file-upload-label:hover {
  background: #f68f9d;
}

.file-input {
  display: none;
}

.file-preview {
  max-width: 200px;
  margin: 1rem auto;
  border-radius: 8px;
}

/* Voice Recorder */
.voice-recorder-container {
  margin: 1.5rem 0;
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 8px;
}

.voice-recorder-controls {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.record-button, .stop-button {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 50%;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  background: #f8a1c2;
  color: white;
}

.record-button:hover, .stop-button:hover {
  transform: scale(1.1);
  background: #f68f9d;
}

/* Audio Preview */
.audio-preview {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.audio-player {
  width: 100%;
  max-width: 300px;
}

.delete-recording-button {
  padding: 0.5rem 1rem;
  background: #f8a1c2;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.delete-recording-button:hover {
  background: #f68f9d;
}

/* Error & Success Messages */
.error-message {
  color: #e94d58;
  margin-top: 1rem;
}

.success-message {
  color: #4CAF50;
  margin-top: 1rem;
}

/* Link and Copy Button */
.link-container {
  display: flex;
  gap: 0.5rem;
  margin: 1rem 0;
}

.generated-link {
  flex: 1;
  padding: 0.75rem;
  border: 2px solid #e1e1e1;
  border-radius: 8px;
  font-size: 0.9rem;
  background: #fff;
}

.copy-button {
  padding: 0.75rem 1.5rem;
  background: #f8a1c2;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.copy-button:hover {
  background: #f68f9d;
}

/* Preview Link */
.preview-link {
  display: inline-block;
  margin-top: 1rem;
  color: #f8a1c2;
  text-decoration: none;
  font-weight: 500;
}

.preview-link:hover {
  text-decoration: underline;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .create-container {
    padding: 1rem;
    width: 100%;
  }

  .link-container {
    flex-direction: column;
  }

  .copy-button {
    width: 100%;
  }

  .file-upload-label {
    padding: 0.6rem 1.2rem;
    font-size: 0.9rem;
  }

  .record-button, .stop-button {
    padding: 0.6rem 1.2rem;
  }
}
